.nav {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 1rem;
  justify-items: center;
}

.nav a,
.sair {
  background: #eee;
  border-radius: 0.2rem;
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid transparent;
  transition: 0.1s;
  cursor: pointer;
}

.nav a:hover,
.sair:hover {
  background: white;
  box-shadow: 0 0 0 3px #eee;
  border-color: #333;
  outline: none;
}

.nav a:global(.active) {
  background: white;
  box-shadow: 0 0 0 3px #fea;
  border-color: #fb1;
}

.nav a:global(.active) svg > * {
  fill: #fb1;
}

@media (max-width: 600px) {
  .mobileButton {
    background: #eee;
    border-radius: 0.2rem;
    height: 40px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid transparent;
    transition: 0.1s;
    cursor: pointer;
    padding: 0;
  }

  .mobileButton::after {
    content: "";
    display: block;
    width: 1.2rem;
    height: 2px;
    border-radius: 2px;
    background: currentColor;
    box-shadow: 0 6px currentColor, 0 -6px currentColor;
    transition: transform 0.2s;
  }

  .mobileButton:focus,
  .mobileButton:hover,
  .mobileButtonActive {
    outline: none;
    background: white;
    box-shadow: 0 0 0 3px #fea;
    border-color: #fb1;
    color: #fb1;
  }

  .mobileButtonActive::after {
    transform: rotate(90deg);
    width: 4px;
    height: 4px;
    box-shadow: 0 8px currentColor, 0 -8px currentColor;
  }

  .navMobile {
    display: block;
    position: absolute;
    top: 70px;
    right: 0;
    padding: 0 1rem;
    background: white;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    border-radius: 0.2rem;
    transform: translate(-10px);
    opacity: 0;
    pointer-events: none;
  }

  .navMobile a,
  .navMobile .sair {
    display: flex;
    align-items: center;
    background: none;
    width: 100%;
    border: none;
    border-bottom: 1px solid #eee;
    padding: 0.5rem;
    cursor: pointer;
  }

  .navMobile svg {
    margin-right: 0.5rem;
  }

  .navMobile .sair {
    border: none;
  }

  .navMobile a:hover,
  .navMobile .sair:hover {
    box-shadow: none;
    border-color: none;
    outline: none;
  }

  .navMobile a:hover svg > *,
  .navMobile .sair:hover svg > * {
    fill: #fb1;
  }

  .navMobileActive {
    transition: 0.3s;
    transform: translate(0);
    opacity: 1;
    z-index: 100;
    pointer-events: initial;
  }
}
